on: none;\n    color: $color-link;\n}\n","$color-light: #f5f5f5;\n$color-black: #000000;\n$color-primary: #ff5a00;\n$color-background: #cbcbcb;\n\n$color-additional: #650e70;\n\n$color-primary-light: #ff8e08;\n\n$color-text: #f5f5f5;\n$color-dark: #303030;\n$color-border: #757575;\n$color-link: #422779;\n$color-hovered: #ff5a00;\n\n$color-body: #f5b668;\n$color-error: #e9a4a2;\n$color-invalid-body: #ede5e5;\n$color-valid: #0ff12e45;\n$color-valid-body: #ffe9084f;\n\n$color-overlay: rgba(80, 80, 80, 0.4);\n$color-shadow: rgba(0, 0, 0, 0.58);\n","html {\n    font-size: 10px;\n    font-family: 'Quicksand', sans-serif;\n\n    @include media-tablet {\n        font-size: 9px;\n    }\n\n    @include mobile-big-width {\n        font-size: 6px;\n    }\n}\n\nbody {\n    background-color: $color-body;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n}\n\nbody > div:first-of-type {\n    width: 90%;\n    margin: 0 auto;\n}\n\n.title {\n    @include heading-1($color: $color-dark, $style: italic);\n    padding: 1rem 0rem;\n}\n\n.basic-text {\n    @include basic-text($color: $color-dark, $style: normal);\n}\n\n.text-register {\n    font-family: 'Quicksand', sans-serif;\n    font-weight: 700;\n    font-size: 1.1rem;\n    margin-bottom: 4%;\n}\n\n/* стилизация глобального контейнера страницы */\n\n.main-box {\n    display: flex;\n    flex-direction: column;\n}\n\n.main-wrap {\n    display: flex;\n    flex-direction: column;\n    background-color: $color-light;\n    align-items: center;\n    justify-content: space-evenly;\n    flex-grow: 1;\n    border: solid 2rem $color-background;\n    flex-basis: 80%;\n}\n\n.main-title {\n    align-items: center;\n    font-size: 3rem;\n}\n","@mixin media-narrowing($media-narrowing) {\n    @media (max-width: $media-narrowing) {\n        @content;\n    }\n}\n\n@mixin desktop-small-width {\n    @media (max-width: $desktop-small-width) {\n        @content;\n    }\n}\n\n@mixin media-tablet {\n    @media (max-width: $tablet-width) {\n        @content;\n    }\n}\n\n@mixin mobile-big-width {\n    @media (max-width: $mobile-big-width) {\n        @content;\n    }\n}\n@mixin mobile-small-width {\n    @media (max-width: $mobile-small-width) {\n        @content;\n    }\n}\n","@mixin heading-1($color: $color-text, $style: normal) {\n    font: {\n        family: 'Open Sans', sans-serif;\n        size: 1.5rem;\n        style: $style;\n        weight: 500;\n    }\n\n    line-height: 105%;\n    color: $color;\n}\n\n@mixin heading-2($color: $color-text, $size: 3.6rem, $style: normal) {\n    font: {\n        family: 'Open Sans', sans-serif;\n        size: $size;\n        style: $style;\n        weight: 500;\n    }\n\n    line-height: 105%;\n    color: $color;\n}\n\n@mixin basic-text($color: $color-text, $style: normal) {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: 6.4rem;\n        style: $style;\n        weight: 600;\n    }\n\n    line-height: 105%;\n    color: $color;\n}\n\n@mixin input-placeholder($color: $color-border, $style: normal) {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: 1.2rem;\n        style: $style;\n        weight: 400;\n    }\n\n    line-height: 105%;\n    color: $color;\n    padding: 0.2rem;\n}\n\n@mixin input-text($color: $color-link, $size: 1.5rem) {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: $size;\n        style: normal;\n        weight: 600;\n    }\n\n    line-height: 105%;\n    color: $color;\n}\n\n@mixin button-text($color: $color-primary, $style: normal) {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: 1.8em;\n        style: $style;\n        weight: 800;\n    }\n\n    line-height: 105%;\n    color: $color;\n}\n\n@mixin input() {\n    width: 21.4rem;\n    height: 3.3rem;\n    border: $color-border 1px solid;\n    box-shadow: 3px 3px 5px -2px rgba(0, 0, 0, 0.75);\n    padding-left: 0.5rem;\n    padding-bottom: 0.5rem;\n    cursor: pointer;\n    @include input-text;\n    &::placeholder {\n        @include input-placeholder($style: italic);\n    }\n}\n\n@mixin button-basic($color-bg: $color-light) {\n    display: block;\n    width: 12rem;\n    height: 4.4rem;\n    background-color: $color-bg;\n    border-radius: 1rem;\n    border: solid 2px $color-border;\n    text-align: center;\n    @include button-text;\n    cursor: pointer;\n\n    &:hover {\n        background-color: $color-additional;\n        color: $color-light;\n    }\n\n    &.active {\n        background-color: $color-primary;\n        color: $color-additional;\n    }\n}\n",".button-basic {\n    @include button-basic;\n}\n",".header {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 20px;\n    padding: 10px 20px;\n    background-color: $color-light;\n}\n\n.header-up {\n    display: flex;\n    width: 100%;\n    justify-content: space-between;\n    align-items: center;\n}\n\n.header-down {\n    display: flex;\n    width: 100%;\n}\n\n.search-box {\n    display: flex;\n    flex-direction: column;\n    width: 50%;\n}\n\n.header-logo {\n    font-size: 4rem;\n    font-weight: 700;\n    color: $color-primary-light;\n    cursor: pointer;\n    @include media-narrowing(800px) {\n        font-size: 2.5rem;\n    }\n}\n\n.btn-header {\n    display: flex;\n    justify-content: flex-end;\n    align-items: center;\n    gap: 10px;\n}\n\n.navigation {\n    display: flex;\n    gap: 5%;\n    @include media-narrowing(800px) {\n        gap: 1rem;\n    }\n}\n\n.navigation a {\n    display: flex;\n    width: 100%;\n    font-size: 2rem;\n    cursor: pointer;\n    white-space: nowrap;\n    position: relative;\n    @include media-narrowing(800px) {\n        font-size: 1.5rem;\n    }\n}\n\n.tagline {\n    font-size: 2rem;\n    color: $color-link;\n    margin-bottom: 1%;\n}\n\n.input-search {\n    display: flex;\n    width: 100%;\n    height: 40px;\n    justify-content: flex-start;\n    gap: 1%;\n}\n\n.main-search {\n    width: 100%;\n    height: 30px;\n    font-size: 1.5rem;\n    border-radius: 2px;\n    padding-left: 1%;\n    border: 1px solid $color-black;\n    flex-shrink: 1;\n}\n\n.btn-search {\n    width: 100px;\n    background-color: $color-primary-light;\n    height: 30px;\n    font-size: 1.5rem;\n    color: $color-text;\n    border: none;\n    border-radius: 5px;\n    margin-bottom: 9%;\n    cursor: pointer;\n}\n\n.btn-filter {\n    width: 100px;\n    background-color: $color-link;\n    height: 30px;\n    font-size: 1.5rem;\n    color: $color-text;\n    border: none;\n    border-radius: 5px;\n    margin-bottom: 9%;\n    cursor: pointer;\n    padding: 1%;\n}\n\n.header-user-name {\n    display: block;\n    height: 20px;\n    font-size: 1.5rem;\n    cursor: pointer;\n    color: $color-black;\n}\n\n.header-user-name:hover {\n    color: $color-primary-light;\n}\n\n.btn-user-login {\n    font-size: 1.5rem;\n    cursor: pointer;\n    background-color: $color-text;\n}\n\n.btn-user-signup {\n    font-size: 1.5rem;\n    cursor: pointer;\n    background-color: $color-primary-light;\n    color: $color-text;\n    border: none;\n    border-radius: 5px;\n    padding: 3%;\n}\n\n.btn-user-logout {\n    display: block;\n    font-size: 1.5rem;\n    cursor: pointer;\n    background-color: $color-primary-light;\n    color: $color-text;\n    border: none;\n    border-radius: 5px;\n    padding: 5%;\n}\n\n/* lines animation */\n\n.navigation a::after {\n    display: block;\n    position: absolute;\n    width: 0;\n    left: 0;\n    top: 28px;\n    height: 2px;\n    background-color: $color-primary-light;\n    content: '';\n    transition: width 0.4s ease-out;\n    cursor: pointer;\n}\n\n.navigation a:hover::after,\n.navigation a:focus::after {\n    width: 100%;\n}\n",".footer {\n    display: flex;\n    height: 100%;\n    padding: 20px;\n    align-items: center;\n    justify-content: space-between;\n    background-color: $color-primary-light;\n}\n\n.nav-footer li {\n    margin-bottom: 5%;\n}\n\n.footer-link {\n    font-size: 1.8rem;\n    color: $color-text;\n}\n\n.left-footer {\n    width: 170px;\n}\n\n.middle-footer {\n    display: flex;\n    align-items: center;\n    gap: 5px;\n    flex-direction: column;\n}\n\n.middle-footer label {\n    color: $color-text;\n    font-size: 1.5rem;\n}\n\n.right-footer {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\n.logo-footer {\n    color: $color-text;\n    font-size: 5rem;\n    font-weight: 700;\n}\n\n.text-footer {\n    color: $color-additional;\n    font-size: 1.5rem;\n}\n",".registration_container {\n    background-color: $color-light;\n    flex-basis: 50%;\n    margin: 15rem;\n    border-radius: 3rem;\n    box-shadow: 10px 10px 25px -4px $color-shadow;\n}\n\n.registration {\n    display: flex;\n    flex-direction: column;\n    padding: 4rem 10rem;\n    gap: 1rem;\n    justify-content: center;\n    align-items: center;\n}\n\n.logo_registration {\n    background-image: url('img/apoint.png');\n    display: block;\n    width: 11.4rem;\n    height: 3.4rem;\n    background-repeat: no-repeat;\n    background-size: contain;\n}\n\n.registration__error {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: 1.2rem;\n        style: normal;\n        weight: 400;\n    }\n\n    line-height: 105%;\n    color: $color-error;\n}\n\n.label_basic {\n    display: flex;\n    gap: 1rem;\n    justify-content: center;\n    align-items: center;\n}\n\n.input_basic {\n    @include input;\n\n    &:focus {\n        background-color: #ff900893;\n        color: $color-primary;\n        border-color: $color-primary;\n        outline: 0;\n    }\n\n    &:invalid {\n        border: 2px solid $color-error;\n        color: $color-dark;\n        background-color: $color-invalid-body;\n    }\n\n    &:valid {\n        border: 2px solid $color-valid;\n        color: $color-primary;\n        background-color: $color-valid-body;\n    }\n}\n\n.svg_user {\n    background-image: url('img/icons/user.svg');\n    display: block;\n    width: 